.arrow-left,
.arrow-left:hover,
.arrow-right,
.arrow-right:hover,
.arrow-prev,
.arrow-prev:hover,
.arrow-next,
.arrow-next:hover {
  display: inline-block;
  background-image: url("../img/sprite.svg");
  background-size: 25.1875em 22.625em;
}

.block__block-with-arrows .block__arrows {
  display: none;
}

.arrow-left {
  width: 1.625em;
  height: 4.1875em;
  background-position: -21.375em -0.3125em;
  transform: rotateY(180deg);
  margin: 0 0.5rem;
}

.arrow-left:hover {
  width: 1.625em;
  height: 4.1875em;
  background-position: -21.375em -5.0625em;
}

.arrow-right {
  width: 1.625em;
  height: 4.1875em;
  background-position: -21.375em -0.3125em;
  margin: 0 0.5rem;
}

.arrow-right:hover {
  width: 1.625em;
  height: 4.1875em;
  background-position: -21.375em -5.0625em;
}

.arrow-prev {
  width: 4.25em;
  height: 1.6875em;
  background-position: -10.8125em -12.9375em;
}

.arrow-prev:hover {
  width: 4.1875em;
  height: 1.625em;
  background-position: -7.3125em -15.4375em;
}

.arrow-next {
  width: 4.25em;
  height: 1.6875em;
  background-position: -16.25em -9.3125em;
}

.arrow-next:hover {
  width: 4.1875em;
  height: 1.625em;
  background-position: -15.625em -12.9375em;
}

@media only screen and (min-width: 1024px) {
  .block__block-with-arrows {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    flex-wrap: nowrap;
  }

  .block__block-with-arrows .block__arrows {
    display: block;
  }
}